<template>
  <ul class="tags">
      <router-link :to="{ name: 'CateList',params:{list:list.name} }"
                   tag="li" 
                   v-for="list in cateList" 
                   :key="list.id">
        <div>
          {{list.name}}
        </div>
        <animate-number from="1" 
                        :to="list.child.length" 
                        duration="1000" 
                        easing="easeOutQuad"
        ></animate-number>
      </router-link>
  </ul>
</template>
<script>
  export default {
    data () {
      return {
  
      }
    },
    props: {
      cateList: {
        type: Array,
        default: []
      }
    },
    methods: {
    }
  }
</script>
<style lang="scss">
  .tags{
    width: 1080px;
    margin: 80px auto 0 auto;
    position: relative;
    &::before{
      content:'Arthropoda Category';
      color:#959EA8;
      position: absolute;
      font-size: 24px;
      top: -40px;
    }
    li{
      display: inline-flex;
      justify-content: space-between;
      align-items: center;
      background: #363D4C;
      color: #959EA8;
      font-size: 14px;
      height: 30px;
      padding: 0 12px;
      margin-top: 6px;
      margin-right: 6px;
      transition: all .3s;
      cursor: pointer;
      &:hover{
        background: #fff;
        color:#735DEE;
      }
      &:active{
        background: darken(#735DEE, 10%);
      }
      span{
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 0 0 0 10px;
        height: 30px;
        margin-left: 10px;
        border-left: 1px solid #2D3239;
      }
    }
  }
</style>





