<template>
  <div class="hello">
    <Tags :cate-list="cateList"></Tags>
    <router-view></router-view>
    <Post></Post>
  </div>
</template>

<script>
import Tags from '@/components/tags'
import Post from '@/components/article'
export default {
  name: 'hello',
  components: {
    Tags,
    Post
  },
  data () {
    return {
      cateList: []
    }
  },
  async created () {
    const list = await this.$axios({
      method: 'get',
      url: '/api/category/list'
    })
    this.cateList = list.data.data
  }
}
</script>

<style scoped>

</style>



