<template>
  <div class="modal-cover" @click="toggleModal">
      <div class="modal" @click.stop>
        <swiper :options="swiperOption" :not-next-tick="notNextTick" ref="mySwiper">
          <swiper-slide v-for="i in 4" :key="i">
            <img src="../assets/cover.png" style="height:510px;">
          </swiper-slide>
          <div class="swiper-pagination" slot="pagination"></div>
        </swiper>
        <IEcharts :option="bar"></IEcharts>
        <a href="javascript:;" class="close" @click.stop="toggleModal">
          <i class="iconfont">&#xe6e9;</i>
        </a>
        <div class="modal-title">
          <h5>Grammostola pulchripes</h5>
        </div>
        <table>
          <tr>
            <td>学名：</td>
            <td>Grammostola pulchripes</td>
            <td>类型</td>
            <td>瓦鸟洞蜘蛛</td>
          </tr>
          <tr>
            <td>中文名：</td>
            <td>巴西巨人金直间</td>
            <td>类别：</td>
            <td>瓦鸟洞蜘蛛</td>
          </tr>
          <tr>
            <td>亚科：</td>
            <td>Aviculariinae</td>
            <td>荨麻疹：</td>
            <td>是</td>
          </tr>
          <tr>
            <td>身长：</td>
            <td class="table-num">≤5-6cm</td>
            <td>跨距：</td>
            <td class="table-num">≤12-15cm</td>
          </tr>
          <tr>
            <td>身长：</td>
            <td>可能温和。还没有宝贵的科学研究。</td>
            <td>成长率：</td>
            <td>快</td>
          </tr>
          <tr>
            <td>预期寿命</td>
            <td>磁性：雄性</td>
            <td colspan="2"></td>
          </tr>
          <tr>
            <td>温度</td>
            <td></td>
            <td colspan="2"></td>
          </tr>
        </table>
        <div class="mapbox" ref="mapbox"></div>
        <div class="content">
          <p>Ephebopus murinus是非常紧张，防御和（在年轻的时候）在闪电般的快速。蜘蛛会先尝试逃跑。持续的挑衅可能导致使用荨麻疹或咬。蜘蛛将在地下挖一个洞，不如其他洞穴物种那么深，加强入口，无论距离表面几厘米处都能找到。当给予一个很好的设置与几个锚点，你可能会期待一个漂亮的网页。关于他们的行为的更多信息</p>
        </div>
  
      </div>
    </div>
</template>
<script>
import mapboxgl from 'mapbox-gl'
import IEcharts from 'vue-echarts-v3/src/lite.vue'
import 'echarts/lib/chart/radar'

mapboxgl.accessToken = 'pk.eyJ1Ijoid2FuZ3dlbmJvIiwiYSI6ImEwTjJOVUEifQ.PSnvBkC79ydlN4yvDs-HSQ'
export default{
  name: 'detial',
  data () {
    return {
      swiperOption: {
        // swiper optionss 所有的配置同swiper官方api配置
        autoplay: 3000,
        loop: true,
        direction: 'horizontal',
        grabCursor: true,
        setWrapperSize: true,
        autoHeight: true,
        pagination: '.swiper-pagination',
        paginationClickable: true,
        mousewheelControl: false,
        observeParents: true,
        // if you need use plugins in the swiper, you can config in here like this
        // 如果自行设计了插件，那么插件的一些配置相关参数，也应该出现在这个对象中，如下debugger
        debugger: true,
        // swiper callbacks
        // swiper的各种回调函数也可以出现在这个对象中，和swiper官方一样
        onTransitionStart (swiper) {
          console.log(swiper)
        }
      },
      loading: true,
      bar: {
        color: ['#87f7cf'],
        textStyle: {
          fontWeight: ['bold'],
          fontFamily: ['Roboto']
        },
        title: {
          text: '基础雷达图'
        },
        tooltip: {
          trigger: 'item',
          backgroundColor: 'rgba(0,0,250,0.2)'
        },
        radar: {
          // shape: 'circle',
          // 足展大小，毒性，寿命，攻击性或者性格，提毛指数，爬行速度
          indicator: [
            { text: '足展大小', max: 6500 },
            { text: '毒性', max: 16000 },
            { text: '寿命', max: 30000 },
            { text: '攻击性或者性格', max: 38000 },
            { text: '提毛指数', max: 52000 },
            { text: '爬行速度', max: 25000 }
          ]
        },
        series: [{
          name: '预算 vs 开销（Budget vs spending）',
          type: 'radar',
          areaStyle: {
            normal: {
              opacity: 0.6,
              color: 'rgba(115, 93, 238, 0.4)'
            }
          },
          data: [
            {
              value: [4300, 10000, 28000, 35000, 50000, 19000],
              name: '预算分配（Allocated Budget）',
              label: {
                normal: {
                  show: true,
                  formatter: params => {
                    return params.value
                  }
                }
              }
            }
          ]
        }]
      }
    }
  },
  components: {
    IEcharts
  },
  mounted () {
    this.$nextTick(() => {
      new mapboxgl.Map({
        container: this.$refs.mapbox,
        style: 'mapbox://styles/mapbox/light-v9',
        center: [-122.420679, 37.772537],
        zoom: 13,
        scrollZoom: false
      })
    })
  },
  methods: {
    toggleModal () {
    }
  }
}
</script>
<style lang='scss' scope>
.modal-cover {
    width: 100%;
    height: 100%;
    position: fixed;
    background: rgba(28, 31, 36, 0.72);
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: 100;
    overflow-y: scroll;
  }
  .modal {
    width: 800px;
    background: #fff;
    position: absolute;
    left: 50%;
    margin-left: -400px;
    top: 100px;
    z-index: 200;
    .close {
      width: 50px;
      height: 50px;
      display: block;
      position: absolute;
      right: 0;
      top: 0;
      z-index: 110;
      color: #fff;
      font-size: 32px;
      display: inline-flex;
      justify-content: center;
      align-items: center;
      background: #735DEE;
    }
    .modal-title {
      height: 56px;
      border-bottom: 1px solid #D6D6D6;
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding-left: 20px;
      h5 {
        font-size: 24px;
      }
    }
    table {
      width: 100%;
      border-collapse: separate;
      border-spacing: 0 0;
      font-size: 14px;
      .table-num {
        font-size: 20px;
      }
      tr {
        td {
          border-bottom: 1px solid #D6D6D6;
          border-right: 1px solid #D6D6D6;
          text-align: left;
          padding: 10px 10px;
          &:nth-child(2n-1) {
            font-weight: 600;
          }
        }
      }
    }
    .mapbox {
      outline: none;
      height: 300px;
      width: 100%;
      .mapboxgl-canvas {
        width: 100%;
      }
    }
    .content {
      padding: 20px;
      font-size: 14px;
      color: #4B4F57;
      letter-spacing: 0.6px;
      line-height: 24px;
      border-bottom: 1px solid #D6D6D6;
      p {
        margin: 0;
      }
    }
    .vue-echarts {
      width: 320px;
      height: 300px;
      position: absolute;
      top: 10px;
      left: 10px;
      z-index: 120;
    }
  }
</style>



